@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #fff;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

:root {
  --blue: #237cff;
}

.header {
  background: #fff;
  align-items: center;
  width: 100%;
  min-height: 80px;
  padding: 0 32px;
  display: flex;
}

.header__logo-link {
  align-items: center;
  margin-right: 40px;
  display: flex;
}

.header__logo {
  width: auto;
  height: 40px;
}

.nav {
  flex: 1;
  margin-left: 500px;
}

.nav__list {
  align-items: center;
  list-style: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 32px;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #222;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  height: 56px;
  padding: 0;
  font-size: 16px;
  text-decoration: none;
  transition: color .2s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--blue);
}

.nav__link.is-active:after {
  content: "";
  background: var(--blue);
  border-radius: 2px;
  height: 3px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}
